.App{
    display: flex;
    flex-flow: row nowrap;
    height: 100%;
    justify-content: center;
    align-items: flex-start;
}

.leftboxhome{
    width: 20%;
    height: 100%;
    /* border-style: solid;
    border-color: black; */
    box-sizing: border-box;
    border-width: 2px;
    display: flex;
    flex-flow: column nowrap;
    flex-grow: 1;
    align-items: flex-end;
}

.rightboxhome{
    flex-grow: 1;
    width: 20%;
    height: 100%;
    border-style: solid;
    border-color: black;
    box-sizing: border-box;
    border-width: 2px;
}

.middleboxhome{
    width: 100%;
    max-width: 1000px;
    min-width: 600px;
    height: 100%;
    /* border-style: solid;
    border-color: black; */
    box-sizing: border-box;
    border-width: 2px;
}